<template>
  <div>
    <div class="col-12 col-lg-6 p-5 h-100 d-flex align-self-center">
      <div>
        <div class="h1 text-uppercase">
          <div :class="{'text-left': leftText, 'text-right': !leftText }">
            <slot name="title"></slot>
          </div>
        </div>
        <div class="text-secondary">
          <slot name="description"></slot>
        </div>
      </div>
    </div>
    <div class="col-12 col-lg-6">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ZigzagBox',

    props: {
      leftText: {
        type: Boolean,
        default: true,
      },
    },

  }
</script>

<style lang="scss" scoped>
  .h1 {
    color: #666;
    border-bottom: 3px solid #f7f7f7;
    padding-bottom: 10px;
  }
</style>



